body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li {
  list-style: none;
}
.route {
  padding: 20px;
}
.route span {
  font-size: 14px;
  color: #1890ff;
  cursor: pointer;
  margin-left: 40px;
}
body {
  background-color: rgb(236, 236, 236);
}
h3, p {
  margin: 0;
}
.card-item {
  position: relative;
  display: inline-block;
  margin-left: 30px;
  width: 260px;
  overflow: hidden;
  border-radius: 10px;
  border: 1px solid #e8e8e8;
  background-color: #fff;
}
.card-item:hover {
  box-shadow: 0 2px 8px rgba(0,0,0,0.09);
  border-color: rgba(0,0,0,0.09);
}
.card-item.active {
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}
.card-title {
  padding: 16px 24px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-bottom: 1px solid #e8e8e8;
}
.card-title h3 {
  display: inline-block;
}
.card-content{
  padding: 24px;
}
.icon-btn {
  float: right;
  font-size: 14px;
  color: #1890ff;
  cursor: pointer;
}
.update-count {
  text-align: center;
}
.pic-item {
  position: relative;
  display: inline-block;
  width: 100px;
  overflow: hidden;
  font-size: 0;
  border-radius: 10px;
  background-size: cover;
  background-repeat: no-repeat;
}

.pic-item::before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  background-color: rgba(0,0,0,0.5);
  transition: all .3s;
  z-index: 1;
}
.pic-item:hover::before {
  opacity: 1;
}
.pic {
  width: 100%;
  width: 100%;
}

.preview-box {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  background-color: #000;
  transition: opacity .36s ease-in-out;
  z-index: 1000;
}
.img {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
  border-radius: 10px;
  z-index: 1002;
}
.img.active {
  transition: all .36s ease-in-out;
}
